import projectTemplate from './template/projectTemplate.hbs';
import projectsInfo from '../projects-info.json';

const refs = {
  overlay: document.querySelector('.js-overlay'),
  projectContainer: document.querySelector('.js-container'),
  worksContainer: document.querySelector('.js-works'),

};

refs.worksContainer.addEventListener('click', handleWorkItemClick);
refs.overlay.addEventListener('click', handleCloseOverlay);

function handleWorkItemClick(event) {
  const { projectContainer, overlay } = refs;
  const clickedElement = event.target;
  const clickedListItem = clickedElement.closest('li');

  if (!clickedListItem.dataset) {
    return;
  }

  const indexOfItem = Number(clickedListItem.dataset.index);
  const currentProjectInfo = projectsInfo[indexOfItem];
  const marckup = projectTemplate(currentProjectInfo);
  projectContainer.insertAdjacentHTML('beforeend', marckup);
  overlay.classList.toggle('overlay-hidden');
}

function handleCloseOverlay(event) {
  const clickedElement = event.target;

  if(!clickedElement.dataset.action){
    return
  }

  refs.overlay.classList.toggle('overlay-hidden');
  setTimeout(() => {refs.projectContainer.innerHTML = '';}, 300);
}