import writeUserData from './services/fetchData';

const refs = {
  form: document.querySelector('.js-form'),
  userName: document.querySelector('.js-name'),
  userEmail: document.querySelector('.js-email'),
  userMessage: document.querySelector('.js-message'),
  donePopup: document.querySelector('.js-done'),
};

refs.form.addEventListener('submit', handleFormSubmit);

function handleFormSubmit(event) {
    event.preventDefault();

    const name = refs.userName.value;
    const email = refs.userEmail.value;
    const message = refs.userMessage.value;

    writeUserData(email, name, message);

    refs.donePopup.classList.toggle('done-hidden');
    setTimeout(() => refs.donePopup.classList.toggle('done-hidden'), 1500);
    refs.userName.value = '';
    refs.userEmail.value = '';
    refs.userMessage.value = '';
}